.widget-area {
  margin-top: 1.5em;
}

.widget {
  margin: 0 0 2em;
  padding: 0 0 2em;
  font-family: $font__open_sans;

  ul,
  ol {
    padding: .7em 0 0;
    margin-bottom: 0;
    list-style-type: none;

    li {
      display: block;
      line-height: 2em;

      ul,
      ol {
        margin-left: .5em;

        @media screen and (min-width: 30em) {
          margin-left: 1em;
        }
      }

    }
  }

  a {
    font-weight: 600;
    text-decoration: none;

    &:hover,
    &:focus {
      border-bottom: 3px solid $color__interactive;
    }
  }

  /* Make sure select elements fit in widgets. */
  select {
    width: 100%;
    height: 2.5em;
    padding: 10px;
    font-family: $font__open_sans;
    border: 1px solid #ccc;
  }

}

.widget-title {
  margin-top: 0;
  margin-bottom: .5em;
  font-size: 1.4em;
}

/* Archive and Categories widgets */
.widget_archive li {
  color: #7D7D7D;
}

.widget_categories .children {
  padding-top: 0;
}

/* Search widget. */
.widget_search .search-submit {
  display: none;
}

/* Pages and Menu widgets */
.widget_pages .children,
.widget_nav_menu .sub-menu {
  padding-top: 0;
  padding-bottom: .5em;
}

.widget_pages,
.widget_nav_menu {

  a {
    display: block;
    padding: 1em 0;
    text-decoration: none;
    border-bottom: 1px dashed hsl(0, 0%, 50%);

    &:hover,
    &:focus {
      text-decoration: none;
      border-bottom: 1px dashed hsl(0, 0%, 50%);
    }
  }

  li {
    padding: 0;

  }

}

/* Meta widget */
.widget_meta li {
  padding: 0;
}

/* Recent comments */
.widget_recent_comments {

  li {
    line-height: 1.3em;
    padding-bottom: .5em;
  }

  a:hover,
  a:focus {
    padding-left: 0;
  }

}

/* RSS widget */
.widget_rss li {
  margin-bottom: 1em;
}

.widget li a.rsswidget {
  padding-right: .5em;
  font-size: 1em;
  line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
  color: #333;
  font-size: .8em;
}

.rssSummary {
  padding: .5em 0;
  font-family: $font__open_sans;
  font-size: .8em;
  line-height: 1.4em;
}

/* Search widget */
.widget_search input[type="search"] {
  box-sizing: border-box;
  width: 100%;
  padding: .5em 1em;
  border: 1px solid #c3c3c3;
  border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
  font-weight: 400;

  caption {
    padding: .5em 0;
    font-size: 1em;
    font-weight: 500;
    text-align: left;
  }

  thead {
    background: transparent;
    color: black;

    th {
      border-bottom-width: 2px;
    }
  }

  td {
    padding: .2em;
    font-size: .8em;
    text-align: center;
    background: white;
    border: 1px solid hsl(0, 0%, 80%);
  }

  th {
    border: none;
    text-align: center;
  }

  .pad {
    background: #eaeaea;
  }

  a {
    font-weight: 900;
    text-decoration: none;
  }

}

/* Text widget */
.textwidget {
  font-size: 80%;
  line-height: 1.3em;
}

/* Footer widget stylings */

.footer-widgets {
  margin: 2em 0 0;
  padding: 1em 0;
  background-color: hsl(0, 0%, 10%);

  @media screen and (min-width: $query_small) {
    padding: 2em 1em;
  }

  .widget {
    flex: 1;
    margin: 0 1em 2em;
    padding-top: 1em;

    .original-title {
      color: hsla(0, 0%, 100%, .5);
    }
  }

  .widget,
  .widget-title,
  .widget a,
  .widget_calendar thead,
  .rss-date,
  .widget_rss cite {
    color: white;
  }

  .widget_calendar td,
  .widget_calendar a {
    color: black;
  }

  @media screen and (min-width: $query_medium) {
    /* Use flex to create a responsive layout */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    /* If only one widget is displayed, make it full width. */
    .widget:only-of-type {
      width: 100%;
    }

    /* If 2 or 3 widgets are displayed, make the second one twice as wide. */
    .widget:nth-last-child(n+2):nth-last-child(-n+3):first-child ~ .widget:nth-child(2) {
      flex: 2;
    }

    /* If 4 or more widgets are displayed, show three across when available. */
    .widget:nth-last-child(n+4),
    .widget:nth-last-child(n+4) ~ .widget {
      width: 30%;
      flex: 1 1 30%;
    }
  }

}


.widget-area {

  ul {
    white-space: nowrap;
    overflow: hidden;
    list-style-type: none;
    padding: 0;
    margin: 0;
    border-top: 4px solid #fff;
    background-color: $color__sites-grey;

  }

  ul:hover {
    background-color: darken($color__sites-grey, 20%);
  }

  p {
    padding: 8px 16px 8px 16px;
    margin: 0;
    font-size: 20px;
    font-weight: bold;
  }

  i {
    padding: 12px 16px 8px 16px;
    margin: 0;
  }

  li {
    overflow: hidden;
    overflow-y: auto;
    max-height: 0;
    transition: .5s max-height ease;

    li:hover {
      background-color: darken($color__sites-grey, 10%);
    }

    ul {
      padding: 0;
      margin: 0;

      li {
        background-color: $color__sites-grey;
        max-height: 300px;
        overflow: hidden;

        p {
          font-size: 1rem;
          display: block;
          color: #000;
          padding: 8px 16px 8px 24px;
          margin: 0;
          text-decoration: none;
          font-weight: normal;
        }
      }
    }
  }
}
@media screen and (min-width: $size__max-width) {
  .show-menu{
    width: 25% !important;
    margin-right: 24px!important;
    opacity: 1!important;
  }
  .small-map{
    width: 75% !important;
  }

  div#mapShortcode {
    width: 100%;
    overflow: hidden;
    transition: width 1s ease;
  }
}

.darken {
  background-color: darken($color__sites-grey, 30%) !important;
}
.show-menu{
  opacity: 1 !important;
  max-height: 1000px !important;
}
li.parent.show {
  max-height: 300px;
  overflow-y: auto;
}
.hide{
  display: none;
}
p.hiden {
  display: none !important;
}